<template>
  <div id="login" class="login login-public">
    <!--<h3 style="margin-bottom: 12px"><router-link to="/home" style="color: white; font-size: 30px">Go 首页</router-link></h3>-->
    <div class="login-box">
      <h1 class="logo">
        <img src="../../assets/img/login/logo.png" alt="">
      </h1>
      <div class="form-box">
        <div class="form-wrap">
          <form autocomplete="off" @submit.prevent="_login()">
            <div class="item mailbox" >
              <input :class="{email: !account}" type="text" autocomplete="off" v-model="account">
            </div>
            <div class="item password">
              <input :class="{password: !secret}" type="password" autocomplete="off" v-model="secret">
              <a @click="_goForget()">
                <img src="../../assets/img/login/forget_text.png" alt="">
              </a>
            </div>
            <input type="submit" value="" class="submit submit-login" @click="_login()" />
            <a class="info" @click="_goRegister()">
              <img src="../../assets/img/login/register_text.png" alt="查看《微信小程序从基础到进阶》开发文档 >" title="查看《微信小程序从基础到进阶》开发文档 >">
            </a>
          </form>
        </div>
      </div>
      <a class="links" :href="$store.state.xcxLink" target="_blank">
        <img src="../../assets/img/login/xcx.png" alt="">
      </a>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { getToken, getTokenInfo, register } from '../../api/request'
import { stateCode, userData } from '../../api/config'

export default {
  name: 'Login',
  data () {
    return {
      account: '', // 用户名
      secret: '' // 密码
    }
  },
  created () {

  },
  methods: {
    _login: function () {
      let emailReg = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/)
      if (!this.account) {
        this.$store.commit('showMessage', {text: '邮箱不能为空', type: 'warn'})
        return
      } else if (!this.secret) {
        this.$store.commit('showMessage', {text: '密码不能为空', type: 'warn'})
        return
      } else if (!emailReg.test(this.account)) {
        this.$store.commit('showMessage', {text: '邮箱格式错误', type: 'warn'})
        return
      }else if (!emailReg.test(this.account)){
        this.$store.commit('showMessage',{text: '请输入有效的邮箱', type: 'warn'})
        return
      }

      let param = {
        'account': this.account,
        'secret': this.secret,
        'type': 100
      }
      // 拿到令牌和令牌信息
      this.$store.commit('showMessage', {text: '登录中...', time: 0 })
      getToken(param).then((data) => {
        userData.token = data.token
        return getTokenInfo()
      }).then((data) => {
        this.$store.commit('hideMessage')
        userData.secret = data
        window.sessionStorage.setItem('wind', JSON.stringify(userData))
        this.$router.push('home')
      }).catch(err => {
        this.$store.commit('showMessage', {text: stateCode[err['error_code']+''], type: 'warn'})
      })
    },
    _goRegister () {
      this.$router.push('register')
    },
    _goForget () {
      this.$router.push('forget')
    }
  },
  components: {}
}
</script>

<style scoped lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  @import "~assets/stylus/variable";
  @import "~assets/stylus/mixin";
  @import "~assets/stylus/login/public";
</style>



