<template>
  <div class="home">
    <v-header></v-header>
    <a class="group-btn" @click="switchMask()"></a>
    <div class="rectangle" id="rec">
      <courseCard v-on:action="lisentCourse" v-for="(item, key, index) in course" :item="item" :key="key"
                  :index="index"></courseCard>
    </div>
    <div class="show-model" v-on:click.self="consoleModel" :class="showModel">
      <div class="show-model-main">
        <div class="course-verify" v-if="modelType === 'courseVerify'">
          <div class="model-main-name">
            课程验证
          </div>
          <form autocomplete="off" style="margin-top: 39px;">
            <div class="item imooc-uid">
              <input type="text" placeholder="慕课网用户ID号" v-model="account">
            </div>
          </form>
          <div class="course-verify-note" style="margin-top: 11px;">
            * 请输入慕课网用户ID号
          </div>
          <div @click="verifyCourse()" class="course-verify-do" style="margin-top: 39px;">
            提交
          </div>
        </div>
        <div class="get-key" v-if="modelType === 'getKey'">
          <div class="get-app-key" style="margin-top: 78px;">
            开发者KEY：{{appKey}}
          </div>
        </div>
        <div class="change-password" v-if="modelType === 'changePassword'">
          <div class="model-main-name">
            修改密码
          </div>
          <form autocomplete="off" style="margin-top: 16px;">
            <div class="item imooc-uid">
              <input type="password" placeholder="原密码" v-model="yPassword">
            </div>
            <div class="item imooc-uid">
              <input type="password" placeholder="新密码" v-model="xPassword">
            </div>
            <div class="item imooc-uid">
              <input type="password" placeholder="确认密码" v-model="rPassword">
            </div>
          </form>
          <div @click="changepwd()" class="course-verify-do" style="margin-top: 21px;">
            提交
          </div>
        </div>
      </div>
    </div>

    <transition name="fade">
      <div class="my-mask" v-if="viewMask" @click.self="viewMask=false">
        <div class="menu">
          <p class="name">{{userInfo.nickname}}</p>
          <p>{{userInfo.email}}</p>
          <p @click="lisentCourse(changePasswordAction)" style="cursor: pointer;">修改密码</p>
          <p style="border-bottom: none; cursor: pointer;">退出登录</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
import courseCard from '../../components/courseCard/courseCard'
import vHeader from '../../components/header/header'
import {courseVerify, getCourseList, changePassword, getUserInfo, getAppKey} from '../../api/request'

export default {
  name: 'Home',
  data() {
    return {
      course: [],
      showModel: 'display',
      modelType: '',
      cId: null,
      userInfo: null,
      account: null,
      viewMask: false,
      appKey: null,
      changePasswordAction: {
        'do': 'show',
        'type': 'changePassword'
      },
      yPassword: null,
      xPassword: null,
      rPassword: null
    }
  },
  created() {
    getCourseList().then((res) => {
      this.course = res
    })
    getUserInfo().then((res) => {
      this.userInfo = res
    })
  },
  methods: {
    switchMask() {
      this.viewMask = !this.viewMask
    },
    _myMask() {
      this.viewMask = true
    },
    lisentCourse: function (action) {
      console.log(action)
      if (action.do === 'show') {
        this.showModel = ''
        this.cId = action.cId
        if (action.type === 'getKey') {
          let param = {
            account: this.userInfo.muid,
            type: 300,
            cid: action.cId
          }
          getAppKey(param).then((res) => {
            console.log(res)
            this.appKey = res.appkey
            this.modelType = action.type
          })
        }
        if (action.type === 'changePassword') {
          this.viewMask = false
          this.modelType = action.type
        }
        if (action.type === 'courseVerify') {
          this.modelType = action.type
        }
      } else {
        this.showModel = 'display'
      }
      console.log(this.modelType)
    },
    consoleModel: function () {
      this.showModel = 'display'
      this.modelType = ''
    },
    changepwd: function () {
      if (!this.yPassword) {
        this.$store.commit('showMessage', {text: '请输入原密码', type: 'warn'})
        return
      } else if (!this.xPassword) {
        this.$store.commit('showMessage', {text: '请输密码', type: 'warn'})
        return
      } else if (!this.rPassword) {
        this.$store.commit('showMessage', {text: '请再输入一次密码', type: 'warn'})
        return
      }
      if (this.xPassword !== this.rPassword) {
        this.$store.commit('showMessage', {text: '两次密码输入不一致', type: 'warn'})
        return
      }
      let param = {
        old_password: this.yPassword,
        new_password: this.xPassword,
        confirm_password: this.rPassword,
        email: this.userInfo.email
      }
      changePassword(param).then((res) => {
        console.log(res)
        if (res.error_code === 0) {
          this.$store.commit('showMessage', {text: '修改成功', type: 'info'})
          this.showModel = 'display'
          this.modelType = ''
        } else if (res.error_code === 10008) {
          this.$store.commit('showMessage', {text: '原密码错误，请重试', type: 'info'})
        } else {
          this.$store.commit('showMessage', {text: '未知错误，请稍后重试', type: 'info'})
        }
      })
    },
    verifyCourse: function () {
      let accountVerify = new RegExp(/^\d{5,10}$/)
      if (!accountVerify.test(this.account)) {
        this.$store.commit('showMessage', {text: '慕课网ID号为5-10位数字', type: 'warn'})
        return
      }
      let param = {
        account: this.account,
        type: 300,
        cid: this.cId
      }
      courseVerify(param).then((res) => {
        if (res.err === 0) {
          this.$store.commit('showMessage', {text: '验证成功', type: 'info'})
        }
        setTimeout(() => {
          this.showModel = 'display'
          this.modelType = ''
        }, this.time)
      }).catch((err) => {
        console.log(err)
        if (err.error_code === '20002') {
          this.$store.commit('showMessage', {text: '对不起！您未购买该课程', type: 'warn'})
        }
      })
    }
  },
  components: {
    'courseCard': courseCard,
    'v-header': vHeader
  },
  activated: {}
}
</script>

<style scoped lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  @import "~assets/stylus/variable";
  @import "~assets/stylus/mixin";
  .home
    .group-btn
      width 29px
      height 29px
      position absolute
      top 25px
      right 5%
      background-image url("../../assets/img/home/Group9@2x.png")
      background-size cover
  .rectangle {
    width: 706px;
    background-color: $color-theme;
    margin: 0 auto;
  }

  .show-model {
    position: fixed;
    left 0;
    top 0;
    z-index 999
    width: 100%;
    height: 100%;
    background-color rgba(0, 0, 0, .5);
  }

  .show-model-main {
    width: 337px;
    height: 312px;
    margin: auto 0;
    background-color: #ffffff;
    border-radius: 4px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -168px;
    margin-left: -168px;
    padding: 40px;
  }

  .display {
    display: none;
  }
  .show{
    display: block;
  }
  .model-main-name{
    width: 100%;
    height: 42px;
    font-family: HanziPenSC-W3;
    font-size: 30px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 42px;
    letter-spacing: 0px;
    color: #2d61b4;
    text-align: center;
  }
  .item
    width: 100%;
    height: 38px;
    background-color: #f5f5f5;;
    border-radius 4px;
    margin-bottom 2px
    display flex
    align-items center
    overflow hidden
    input
      font-family: HanziPenSC-W3;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 0px;
      letter-spacing: 0px;
      color: #cacaca;
      padding-left 16px
      flex 1
      width 100%
      height 100%
      background-color: #f5f5f5;
  .imooc-uid{
  }
  .course-verify-note {
    width: 100%;
    height: 17px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 12px;
    letter-spacing: 0px;
    color: #40454a;
    text-align left;
  }
  .course-verify-do {
    width: 257px;
    height: 38px;
    background-color: #2c61b4;
    border-radius: 4px;
    margin: 0 auto;
    line-height: 38px;
    color: #fff;
    font-family: PingFangSC-Medium;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    cursor: pointer;
  }
  .course-verify-do:hover{
  }
  .get-app-key {
    width: 100%;
    height: 17px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 17px;
    letter-spacing: 0px;
    color: #40454a;
    text-align: center;
  }
  // my-mask
  .my-mask
    position fixed
    left 0
    top 0
    z-index 999
    width 100%
    height 100%
    background-color rgba(0,0,0,.3)
    .menu
      position absolute
      top: 60px
      right 5%
      width: 191px;
      height: 186px;
      background-color: #ffffff;
      border-radius: 4px;
      padding 0 10px
      box-sizing border-box
      p
        font-family: PingFangSC-Regular;
        font-size: 14px;
        line-height 40px
        text-align center
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #363636;
        border-bottom 1px solid #dedede
      p.name
        line-height 1em
        margin-top 24px
        border-bottom none
  @media screen and (max-width: $xs) {
    .rectangle {
      width: 100%;
      height: 100%;
    }
  }
</style>



