<template>
  <div id="register" class="register login-public">
    <!--<h3 style="margin-bottom: 12px"><router-link to="/home" style="color: white; font-size: 30px">Go 首页</router-link></h3>-->
    <div class="register-box">
      <h1 class="logo">
        <img src="../../assets/img/login/logo.png" alt="">
      </h1>
      <div class="form-box">
        <div class="form-wrap">
          <form autocomplete="off" @submit.prevent="_register()">
            <div class="item mailbox" >
              <input type="text" :class="{nickname: !nickname}" v-model="nickname">
            </div>
            <div class="item mailbox" >
              <input type="text" :class="{email: !account}" v-model="account">
            </div>
            <div class="item mailbox" >
              <input type="password" autocomplete="off" :class="{password: !secret}" v-model="secret">
            </div>
            <input type="submit" value="" class="submit submit-register" @click="_register()" />
            <a class="info" @click="_goLogin()">
              <img src="../../assets/img/login/login_text.png" alt="">
            </a>
          </form>
        </div>
      </div>
      <a class="links" :href="$store.state.xcxLink" target="_blank">
        <img src="../../assets/img/login/xcx.png" alt="查看《微信小程序从基础到进阶》开发文档 >" title="查看《微信小程序从基础到进阶》开发文档 >">
      </a>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { register } from '../../api/request'
import message from '../../components/message/message'
import { stateCode } from '../../api/config'

export default {
  data () {
    return {
      account: '', // 邮箱
      secret: '', // 密码
      type: 100,
      nickname: '' // 昵称
    }
  },
  created () {

  },
  methods: {
    _register: function () {
      let emailReg=new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
      if(!this.nickname){
        this.$store.commit('showMessage',{text: '昵称不能为空', type: 'warn'})
        return
      }else if(!this.account){
        this.$store.commit('showMessage',{text: '邮箱不能为空', type: 'warn'})
        return
      }else if(!this.secret){
        this.$store.commit('showMessage',{text: '密码不能为空', type: 'warn'})
        return
      }else if (!emailReg.test(this.account)){
        this.$store.commit('showMessage',{text: '请输入有效的邮箱', type: 'warn'})
        return
      }
      let param = {
        account: this.account, // 邮箱
        secret: this.secret, // 密码
        type: this.type,
        nickname: this.nickname // 昵称
      }
//      let param = {
//        account: '2285901508@qq.com', // 账号
//        secret: 123456, // 密码
//        type: this.type,
//        nickname: '2285901508@qq.com' // 昵称
//      }

      register(param).then((res)=>{
        this.$router.push('home')
        this.$store.commit('showMessage', {text: stateCode[res['error_code']+''], time: 3500})
      }).catch(err => {
        this.$store.commit('showMessage', {text: stateCode[err['error_code']+''], type: 'warn'})
      })

    }, // 注册
    _goLogin(){
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  @import "~assets/stylus/variable";
  @import "~assets/stylus/mixin";
  @import "~assets/stylus/login/public";

  #register
    .submit-register
      background-image url("../../assets/img/register/register.png")
      background-repeat no-repeat
      background-size 33px 17px
      background-position center center
    .submit-login:hover
      background-image url("../../assets/img/register/register_hover.png")



</style>



